.colorHover {
  &--secondary {
    &:hover {
      color: $secondary !important; } } }

.check-style-none {
  &:checked {
    box-shadow: none; }
  &:focus {
    box-shadow: none; } }

.transform-y-1 {
  &:hover {
    transform: translateY(-5px); } }

.transition-duration-1 {
  transition-duration: .2s; }
